<script>
import elementnotific from "@/views/elementnotific"
import websocket from "@/views/websocket"
import { getPreMonth } from '@/utils/index.js'
import axios from 'axios';
import api from '@/utils/api.js'
import http from '@/utils/http.js'
export default {
  name: 'HelloWorld',
  components: {
    websocket,elementnotific
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      outerVisible:false,
      i:"0",
      adata:[],
      edata:[],
      month:getPreMonth(),
      comment:''
    }
  },
  methods:{
    showalarm(){
        this.adata.unshift({id:"第"+this.i+"个错误",message:"第"+this.i+"个错误"});
        this.edata={id:"第"+this.i+"个错误",message:"第"+this.i+"个错误"};
        this.i++;
    },
    clickNotify () {
      if (this.comment.length>5) {
          this.$message({
						type: 'error',
						message: '不要超过500字符',
						duration: 1000,
					})
          return false
        }
      this.myNotify({
        content: 'this is a notification'
      })
    },
    fachu(){
      var _data = 'data:image/png;base64,'
      //window.atob方法将其中的base64格式的图片转换成二进制字符串；若将转换后的值直接赋值给Blob会报错，需Uint8Array转换：最后创建Blob对象；
      _data = _data.split(',')[1];
      _data = window.atob(_data);
      
      //如果不用ArrayBuffer,发送给服务器的图片格式是[object Uint8Array],上传失败...
      var _buffer = new ArrayBuffer(_data.length);
      var _ubuffer = new Uint8Array(_buffer);
      // axios.get(api.getUser,{
      //   params:{
      //     ID:12345
      //   }
      // })
      // .then(function(response){
      //   console.log(response);
      // })
      // .catch(function(err){
      //   console.log(err);
      // });
      for (var i = 0; i < _data.length; i++) {
        _ubuffer[i] = _data.charCodeAt(i);
      }
      console.log(_ubuffer);
      var b=[];
      b= Array.from(_ubuffer);
      console.log(b);
      axios.post(api.getUser,{
        ints:b
      })
      .then(function(res){
        console.log(res);
      })
      .catch(function(err){
        console.log(err);
      });
      var self =this;
      var params = new FormData()
						params.append('ints', b);
						http.post(api.getUser, params, {
							headers: {
								"Content-Type": "multipart/form-data"
							},
						}, function(response) {
							if(response.result) {
								self.$message({
									type: 'success',
									message: '新增成功，页面即将跳转至列表页',
									duration: 500,
									onClose: function() {
										self.$router.push({
											path: '/tollgateDevice/list'
										})
									}
								});

							} else {
								self.$message({
									type: 'error',
									message: '新增失败',
									duration: 1000
								});
							}
						}, function(response) {
							//失败回调
						})

    }
  },
  mounted(){
      
  }
}
</script>

