<template>
    <div>sss</div>
    
</template>

<script>
import HelloWorld from '../components/guangdongshuzi.vue'
import { setInterval, setTimeout } from 'timers';
export default {
  name: 'elementnotific',
  props: {
    outerVisible: Boolean,
    //data:Object
   },
  data () {
    return {
      websock:null,
      timeout:false,
      data:[]
    }
  },
  components: {
    HelloWorld
  },
  mounted() {
       this.initWebSocket();
        this.shownotify();
  },
  watch:{
      data(){
         this.shownotify();
      }
  },
  methods:{
      
      shownotify(){
          var self = this;
          console.log(self.data)
         if (self.data.length==0) {
            return
         }else{
           this.$notify.error({
                title: '错误'+self.data.id,
                message: self.data.message,
                position: 'bottom-right'
            });
         }
      },
      initWebSocket(){
        var _self = this;
        var currentTime = new Date();
        _self.websock = new WebSocket("ws://123.207.167.163:9010/ajaxchattest?openid="+_self.openid+"&date="+currentTime);
        _self.websock.onopen = this.websocketonopen;
        _self.websock.onerror = this.websocketonerror;
        _self.websock.onmessage = this.websocketonmessage;
        _self.websock.onclose = this.websocketclose;
      },
      websocketonopen(){
        var _self = this;
        var param = {};
        param["openid"] = _self.openid;
        param["text"] = "Hi";
        param["uuid"] = _self.uuid;
        param = JSON.stringify(param);
        this.websocketsend(param);
      },
      websocketsend(agentData){//数据发送
        this.websock.send(agentData);
      },
      websocketonerror(e){
        var _self = this;
        _self.websock = null;
      },
      websocketonmessage(e){
        let self = this;
       // alert(e.data);
        var msg =JSON.stringify(e.data) ;
        console.log(msg);
        self.data={id:1,message:msg};
       
        
        // var obj = {};
        // obj['id'] = 1;
        // obj['message'] = msg.text;
        // console.log(object);
        // this.addDialog.push(obj);

        //存我发出的消息
        //this.$store.commit('addDialog',obj);
      },
      //销毁websocket
      websocketclose(){
        var _self = this;
        _self.websock = null;
      },
        handleClose(){
            this.outerVisible = false;
        }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/scss" lang="scss" scoped>

    
</style>



