import telephoneComponent from './telephone.vue'
 
const telephone={
    install:function(Vue){
        Vue.component('Telephone',telephoneComponent)
    }  //'Loading'这就是后面可以使用的组件的名字，install是默认的一个方法
};
export default telephone;


