<style scoped>

</style>
<template>
	<div>
        <object id="ACTxObj" name="ACTxObj" style="visibility: visible; width:0px; height: 0px;"
                codebase="http://www.easywork.com.cn/AladdinCTClient.cab#version=1,0,0,0"
                classid="CLSID:64C08D98-2159-48EC-9CEC-3D2265019A61"></object>
        
    </div>
</template>

<script>
    let m_CallWin = null;
    let m_CallIn = null;
    let m_CallOutRecFile = null;
    let m_Guid = null;
    let m_CallType = "";
    let m_CallOutStart = null;
    let ACTxObj = null;
    let m_Id = null;
    let serverIP = null;
    let m_CallNumber ="";
    let m_CallOutEnd= null//呼出结束的时间 
    let m_CallInStart = null;//呼入开始的时间
    let m_CallInEnd= null//呼出结束的时间
    let m_CallInRecFile = null;//呼入录音文件地址
	export default {
		data() {
			return {
				
			}
		},
		props: {
			 
		},
		watch: {

		},
		mounted() {
             
             var self=this;
              self.$http.get(self.api.getCallCenterInfo, {
              params: {
                
                }
              }, function (response) {
                  m_Id = response.data[0].extNumber;
                  serverIP =response.data[0].serverIP
                 self.onLoad()
              }, function (response) {
                //失败回调
              })
        },
        beforeRouteLeave(to, from, next) {
            this.onUnLoad();
            next();
        },
		methods: {
            bangding(){
                document.getElementById("ACTxObj").attachEvent("OnStateChange(AoldState,ANewState,StateCode)",function(){this.OnStateChange(AoldState, ANewState, StateCode);});
                document.getElementById("ACTxObj").attachEvent("OnStatusChange(AStatus,StatusCode)",function(){this.OnStatusChange(AStatus, StatusCode);});
                document.getElementById("ACTxObj").attachEvent("OnError(err)",function(){this.OnError(err);});
                document.getElementById("ACTxObj").attachEvent("OnCallin(ACallerID,AParam,TalkType)",function(){this.OnCallin(ACallerID, AParam, TalkType);});
                
                document.getElementById("ACTxObj").attachEvent("OnExtState(info)",function(){this.OnExtState(info);});
                document.getElementById("ACTxObj").attachEvent("OnOpHangup",function(){this.OnOpHangup()});
                document.getElementById("ACTxObj").attachEvent("FOnHangupCall",function(){this.FOnHangupCall();});
                document.getElementById("ACTxObj").attachEvent("OnAllExtState(str)",function(){this.OnAllExtState(str);});

                document.getElementById("ACTxObj").attachEvent("OnLoginAgain",function(){this.DispStatus();});
                document.getElementById("ACTxObj").attachEvent("OnOutBound",function(){this.DispStatus();});
                document.getElementById("ACTxObj").attachEvent("OnQueueMessage",function(){this.OnQueueMessage(QueryMsg);});
                document.getElementById("ACTxObj").attachEvent("OnAnswer(ARecFile,TalkType)",function(){this.OnAnswer(ARecFile, TalkType);});
                document.getElementById("ACTxObj").attachEvent("OnOpAnswer",function(){this.OpAnswer();});
          
            },
             onLoad() {
	            //document.execCommand('Refresh');
                    
                    ACTxObj = document.getElementById("ACTxObj");
                    this.LogIn();
                //alert(sprintf("%t", new Date()));
                //var time = new Date();
                //alert(sprintf("%d-%2d-%2d %2d:%2d:%2d", time.getFullYear(), time.getMonth() + 1, time.getDate(), time.getHours(), time.getMinutes(), time.getSeconds()));
                //OutCall("9624110");
                if (m_CallNumber.length > 1)
                {//alert(m_CallNumber);
                    //alert("call number: " + m_CallNumber);
                    this.OutCall(m_CallNumber);
                    m_Guid = this.newGuid();
                var iWidth=700;
                var iHeight=400;
                var iTop = (window.screen.availHeight-30-iHeight)/2;
                var iLeft = (window.screen.availWidth-10-iWidth)/2; 
                    //m_CallWin = window.open("qdjb.asp?guid="+m_Guid+"&number="+m_CallNumber,"height="+iHeight+", width="+iWidth+", top="+iTop+", left="+iLeft);
                    m_CallWin = window.open("qdjb.asp?guid="+m_Guid+"&number="+m_CallNumber,"","height="+iHeight+", width="+iWidth+", top="+iTop+", left="+iLeft);
                    //alert(m_CallNumber);
                }
            },
            onUnLoad() {
                this.LogOut();
            },
            LogIn() {
                    
                ACTxObj.ServerIP = serverIP;
                ACTxObj.ExtNumber = m_Id;
           
                var nRet = ACTxObj.Login("", m_Id, m_Id);
                if (nRet == false) {
                    //alert(ACTxObj.ErrorInfo);
                    this.Logs(ACTxObj.ErrorInfo);
                }
                else {
                    //agentState.innerHTML = "登陆-空闲";
                ACTxObj.SetIdle();
                    this.Logs("登陆成功!");
                }
            },
            LogOut() {
                //agentState.innerHTML = "登出-离线";
                ACTxObj.SetBusy("离席", 0);
                if (ACTxObj.LogOut())
                    this.Logs("登出/注销");
                else
                    this.Logs("登出/注销失败");
            },
            OutCall(telenum) {
                console.log(telenum)
                if (ACTxObj.MakeCall(telenum, "", "", "", "")) {
                    this.Logs("拨号:" + telenum);
                }
                //else alert("MakCall false");
                m_CallType = "out";
                m_CallOutStart = new Date();
            },
            DispStatus() {
                //extState.innerHTML = ACTxObj.ExtState;
                //agentState.innerHTML = ACTxObj.AgentState;
                //if (ACTxObj.IsCallin == true)
                //    phoneNum.innerHTML = ACTxObj.CallinData + ":" + ACTxObj.CallerID;
                //window.status =str;
                this.Logs(ACTxObj.ExtState+ACTxObj.AgentState+ACTxObj.CallerID);
            },
            OnStateChange(AoldState, ANewState, StateCode) {
                //  分机状态 ACTxObj.ExtState;
                //DispStatus();
                //extState.innerHTML = AoldState + ">>>" + ANewState;
                this.Logs("OnStateChange " + AoldState + " --> " + ANewState + " -- " + StateCode);
                //if(ANewState=="对方挂机")
                if (ANewState == "呼出通话中") {
                    isTalked = true;
                    //DialTime();
                }
                else {
                    isTalked = false;
                    t1 = 0;
                    t2 = 0;
                    t3 = 0;
                }
                //alert("type: "+m_CallType+", OnStateChange " + AoldState + " --> " + ANewState + " -- " + StateCode);
                if (AoldState == "呼入通话中" && ANewState == "挂机") {
                    if (m_CallType == "in")
                        onHangupIn();
                    else if (m_CallType == "out")
                        onHangupOut();
                } else if (AoldState == "呼出通话中" && ANewState == "挂机") {
                    onHangupOut();
                }
            },
            OnStatusChange(AStatus, StatusCode) {
                //  坐席状态 ACTxObj.AgentState;
                //DispStatus();
                this.Logs("OnStatusChange " + AStatus + "-" + StatusCode);
                //if(agentState.innerHTML!="呼叫通话中" && AStatus=="空闲") extState.innerHTML="外线挂机";  //改了 StateChange 中有 "对方挂机" 消息了
                //agentState.innerHTML = AStatus;
            },
            OnCallin(ACallerID, AParam, TalkType) {
                //   来电呼入 ACTxObj.CallinData+":"+ACTxObj.CallerID;
                //DispStatus();
                var s;
                switch (TalkType) {
                    case 1: s = "分机直拨"; break;
                    case 2: s = "分机转接"; break;
                    case 3: s = "外线直拨"; break;
                    case 4: s = "转分机群组"; break;
                    case 5: s = "转坐席群组"; break;
                    case 6: s = "会议"; break;
                    case 7: s = "代接"; break;
                    case 8: s = "转外线"; break;
                    case 9: s = "预测外拨"; break;
                }
                this.Logs("OnCallin 呼入:" + ACallerID + "   <" + AParam + "> - " + s);
                //alert("OnCallin 呼入:" + ACallerID);
                m_CallIn = ACallerID;
                m_CallType = "in";
                
                //phoneNum.innerHTML = "呼入:" + ACallerID + "   <" + AParam + "> - " + TalkType + ":" + s;
            },
            OnQueueMessage(QueryMsg) {
                this.Logs("OnQueueMessage " + QueryMsg);
            },
            OnError(err) {
                this.Logs("OnError " + ACTxObj.ErrorInfo + "    Err:" + err);
                //alert(ACTxObj.ErrorInfo + "\r\nErr:" + err);
            },
            OnExtState(info) //分机状态
            {
                var arr = info.split("|");
                if (arr[0] == ACTxObj.ExtNumber) {
                    //extState.innerHTML = arr[4];
                    //this.Logs("OnExtState:" + info);
                }
                this.Logs("OnExtState " + info);
                //	alert(info);
            },
            HangupCall() //挂机
            {
                ACTxObj.HangupCall(0);
            },
            OnOpHangup() //对方挂断
            {
            // if (m_CallIn.length > 0 && m_CallWin != null)
                //{
                //   m_CallWin.close();
                //   m_CallWin = null;
                //  alert("电话已经挂断，是否回复？");
                // OutCall("9" + m_CallIn);
                //}
                DispStatus();
                //alert("call type:" + m_CallType);
                if (m_CallType == "in")
                    onHangupIn();
                else if (m_CallType == "out")
                    onHangupOut();
                this.Logs("OnOpHangup 对方挂断");
            },
            OnAllExtState(agentInfoList) //得到所有分机状态
            {
                var str;
                if (agentInfoList == "AllExtStateEnd") {
                    //str = agentInfoListBox.replace(/\n/g, "<br />") + "AllExtStateEnd<br />";
                    //LogBox.innerHTML = str + "<br>" + LogBox.innerHTML;
                    this.Logs(agentInfoListBox);
                }
                else {
                    //agentInfoListBox += agentInfoList;
                }
            },
            FOnHangupCall() //坐席主动挂断
            {
                //extState.innerHTML += "坐席挂断";
                this.Logs("FOnHangupCall 坐席挂断  时长 " + Time.innerHTML);
            },
            onHangupReady() {
                if (xmlHttp.readyState == 4) {
                    //添加常用信息成功
                    //alert(xmlHttp.responseText);
                }
                else
                {
                }
            },
            onHangupIn()
            {
                if (m_Guid == null)
                    return;
                xmlHttp = new XMLHttpRequest();
                var url = "ldjs.asp?guid=" + m_Guid + "&endtime=" + sprintf("%t", new Date());
                xmlHttp.open("GET", url, true);
                xmlHttp.onreadystatechange = onHangupReady;
                xmlHttp.send();
                m_Guid = null;
            },
            onHangupOut()
            {    //alert(sprintf("%t", new Date()));
                if (m_Guid == null || m_CallOutRecFile == null)
                    return;
                xmlHttp = new XMLHttpRequest();
                var url = "qdjs.asp?guid=" + m_Guid + "&starttime=" + sprintf("%t", m_CallOutStart) + "&endtime=" + sprintf("%t", new Date()) +"&recfile=" + m_CallOutRecFile;
                xmlHttp.open("GET", url, true);
                xmlHttp.onreadystatechange = onHangupReady;
                xmlHttp.send();
                m_Guid = null;
            },
            OnAnswer(ARecFile, TalkType)  //应答/接听
            {
                var s;
                switch (TalkType) {
                    case 1: s = "分机直拨"; break;
                    case 2: s = "分机转接"; break;
                    case 3: s = "外线直拨"; break;
                    case 4: s = "转分机群组"; break;
                    case 5: s = "转坐席群组"; break;
                    case 6: s = "会议"; break;
                    case 7: s = "代接"; break;
                    case 8: s = "转外线"; break;
                    case 9: s = "预测外拨"; break;
                }
                //RecFile.value = ARecFile;
                this.Logs("OnAnswer 呼入接听/应答 录音文件:" + ARecFile + "  TalkType:" + s);
                    //alert("呼入接听/应答\n录音文件:"+ARecFile+"\nTalkType:"+s);
                m_Guid = newGuid();
                var strDate = sprintf("%t", new Date());
                var iWidth=1000;
                var iHeight=800;
                var iTop = (window.screen.availHeight-30-iHeight)/2;
                var iLeft = (window.screen.availWidth-10-iWidth)/2; 
                //alert(strDate);
                m_CallWin = window.open("ldjb.asp?ldhm="+m_CallIn+"&ldly="+ARecFile+"&guid="+m_Guid+"&ldsj="+strDate,"","height="+iHeight+", width="+iWidth+", top="+iTop+", left="+iLeft);
                //window.showModelessDialog("ldjb.asp?ldhm="+m_CallIn+"&ldly="+ARecFile+"&guid="+m_Guid+"&ldsj="+strDate,"","dialogWidth:700px;dialogHeight:400px;scroll:no;status:no");
            },
            OpAnswer() {
                //RecFile.value = ACTxObj.TalkRecFile;
                this.Logs("OpAnswer 呼出应答 录音文件:" + ACTxObj.TalkRecFile);
                m_CallOutStart = new Date();
                m_CallOutRecFile = ACTxObj.TalkRecFile;
                //	alert("呼出应答");
            },
            Logs(str) {
                var d = new Date();
                //LogBox.innerHTML = d.toLocaleString() + " " + str + "<br>" + LogBox.innerHTML;
                //alert(d.toLocaleString() + ": " + str);
                console.log(d.toLocaleString() + ": " + str)
            },
            newGuid() {
                var guid = "";
                for (var i = 1; i <= 32; i++) {
                    var n = Math.floor(Math.random() * 16.0).toString(16);
                    guid += n;
                    if ((i == 8) || (i == 12) || (i == 16) || (i == 20))
                        guid += "-";
                }
                return guid;
            },
            DateToStr(time, interval) {
                var str = String(time.getFullYear()) + interval + IntToStr(time.getMonth() + 1, 2) + interval +
                IntToStr(time.getDate(), 2) + interval + IntToStr(time.getHours(), 2) + interval +
                IntToStr(time.getMinutes(), 2) + interval + IntToStr(time.getSeconds(), 2);
                return str;
            },
            sprintf() {
                if (arguments.length == 0)
                    return "";
                
                var str = arguments[0];
                var pos = 0;
                var parampos = 1;
                var strOut = "";
                while (pos < str.length){
                    if (str[pos] == '%'){
                        if (pos + 1 >= str.length)
                            return strOut;
                        var char = str[pos + 1];
                        switch(char)
                        {
                            case 's':
                                strOut += arguments[parampos];
                                parampos++;
                                break;
                            case 'd':
                                strOut += String(arguments[parampos]);
                                parampos++;
                                break;
                            case 't':
                                var time = arguments[parampos];
                                //time = new Date();
                                strOut += sprintf("%d-%2d-%2d %2d:%2d:%2d", time.getFullYear(), time.getMonth() + 1, time.getDate(),
                                    time.getHours(), time.getMinutes(), time.getSeconds());
                                break;
                            default:
                                var i = 0;
                                while (pos + i < str.length)
                                {
                                    i++;
                                    if (str[pos + i] >= '0' && str[pos + i] <= '9')
                                    {
                                        continue;
                                    }
                                    if (str[pos + i] == 'd')
                                    {
                                        var strnum = str.substring(pos + 1, pos + i);
                                        var num = parseInt(strnum);
                                        strnum = String(arguments[parampos]);
                                        if (strnum.length < num)
                                        {
                                            for (z=0; z<num - strnum.length; z++)
                                            {
                                                strOut += '0';
                                            }
                                        }
                                        strOut += strnum;
                                        parampos++;
                                        pos += i - 1;
                                        break;
                                    }
                                }
                                break;
                        }
                        pos += 2;
                    }
                    else
                    {
                        strOut += str[pos];
                        pos++;
                    }
                }
                return strOut;
            }
        }
    }
</script>



