<template>
    <div class="alertDialog" v-if="data&& JSON.stringify(data)!='{}'">
        <div class="errordiv"  v-for="(item, index) in data" >
            <el-alert
                :title='item.message'
                type="error"
                @close="closealert(item.id)"
                :description="item.message"
                show-icon>
            </el-alert>
        </div>
    </div>
    
</template>

<script>
import HelloWorld from '../components/guangdongshuzi.vue'
import { setInterval, setTimeout } from 'timers';
export default {
  name: 'websocket',
  props: {
    outerVisible: Boolean,
    data:Array
   },
  data () {
    return {
      websock:null,
      addDialog:[
          {
              id:0,
              message:"错误一"
          },
          {
              id:1,
              message:"错误2"
          }
      ],
      timeout:false
    }
  },
  components: {
    HelloWorld
  },
  mounted() {
       this.initWebSocket();
     //   setInterval(this.autodelalert,2000);
        this.mesgNotice();
  },
  watch:{
      data(){
        // setTimeout(this.autodelalert,2000);
      }
  },
  methods:{
      mesgNotice(){
            if(window.Notification && Notification.permission !== "denied") {
                Notification.requestPermission(function(status) {
                    var notice_ = new Notification('新的消息', { body: '您有新的消息'});
                    notice_.onclick = function() {//单击消息提示框，进入浏览器页面
                        window.focus();
                    }
                });
            }   
        },
      closealert(id){
          console.log(id);
      },
      autodelalert(){
          if (this.data.length==0 ) {
              return
          }
           this.data.pop()
      },
      initWebSocket(){
        var _self = this;
        var currentTime = new Date();
        _self.websock = new WebSocket("ws://139.198.122.141:8087/ws");
        _self.websock.onopen = this.websocketonopen;
        _self.websock.onerror = this.websocketonerror;
        _self.websock.onmessage = this.websocketonmessage;
        _self.websock.onclose = this.websocketclose;
      },
      
      websocketonopen(){
        var _self = this;
        var param = {};
        param["openid"] = _self.openid;
        param["text"] = "Hi";
        param["uuid"] = _self.uuid;
        param = JSON.stringify(param);
        console.log(this.websocket)
        this.websocketsend(param);
      },
      websocketsend(agentData){//数据发送
      console.log(agentData)
        this.websock.send(agentData);
      },
      websocketonerror(e){
        var _self = this;
        console.log(e)
        _self.websock = null;
      },
      websocketonmessage(e){
        var msg = JSON.parse(e.data);
        var obj = {};
        obj['id'] = 1;
        obj['message'] = msg.Text;
        this.addDialog.push(obj);
        //存我发出的消息
        //this.$store.commit('addDialog',obj);
      },
      //销毁websocket
      websocketclose(){
        var _self = this;
        _self.websock = null;
      },
        handleClose(){
            this.outerVisible = false;
        }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/scss" lang="scss" scoped>
.alertDialog{
    position: fixed;
    left: 0;
    top: 0;
    // height: 100%;
    // max-height: 100%;
    overflow: auto;
    width: 30%;
    max-width: 300px;
    background-color: transparent;
    /* border-right: 2px solid #25170f; */
    /* box-shadow: 0 0 5px #ccc; */
    padding: 0;
    /* opacity: 0; */
    /* transform: translateX(-100%); */
    transition: 0.3s;
    .errordiv{
        margin: 10px;
    }
}
    
</style>



