import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Telephone from '@/components/telephoneCorrelation/telephone'
Vue.use(Router)
const TransportUnitDetail =r =>require.ensure([], () => r(require('@/views/401')), 'InspectionSummary')
const huashutuxiang =r =>require.ensure([], () => r(require('@/views/huashutuxiang')), 'InspectionSummary')
const liuxin =r =>require.ensure([], () => r(require('@/views/liuxin')), 'InspectionSummary')
const sin =r =>require.ensure([], () => r(require('@/views/sin')), 'InspectionSummary')
const d3 =r =>require.ensure([], () => r(require('@/views/3D')), 'InspectionSummary')
const swiper =r =>require.ensure([], () => r(require('@/views/swiper')), 'InspectionSummary')
const rain =r =>require.ensure([], () => r(require('@/views/rain')), 'InspectionSummary')
const websocket =r =>require.ensure([], () => r(require('@/views/websocket')), 'InspectionSummary')
const elementnotific =r =>require.ensure([], () => r(require('@/views/elementnotific')), 'InspectionSummary')
const shiping =r =>require.ensure([], () => r(require('@/views/shiping')), 'InspectionSummary')
const elment =r =>require.ensure([], () => r(require('@/views/elment')), 'InspectionSummary')
 const yanzhen =r =>require.ensure([], () => r(require('@/views/yanzhen')), 'yanzhen')
 const daochuEXl =r =>require.ensure([], () => r(require('@/views/daochuEXl')), 'yanzhen')
 const love =r =>require.ensure([], () => r(require('@/views/love')), 'yanzhen')
 const circleMenu =r =>require.ensure([], () => r(require('@/views/circleMenu')), 'yanzhen')
 const yuanhuan =r =>require.ensure([], () => r(require('@/views/yuanhuan')), 'yanzhen')
 const table =r =>require.ensure([], () => r(require('@/views/table')), 'yanzhen')
 const table2 =r =>require.ensure([], () => r(require('@/views/table2')), 'yanzhen')
 const Menu =r =>require.ensure([], () => r(require('@/views/Menu')), 'yanzhen')
 const Menuc =r =>require.ensure([], () => r(require('@/views/Menu/Menuc')), 'yanzhen')
 const lunxu =r =>require.ensure([], () => r(require('@/views/lunxu')), 'lunxu')
 const qc =r =>require.ensure([], () => r(require('@/views/qc')), 'lunxu')
export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/Telephone',
      name: 'Telephone',
      component: Telephone
    },
    {
      path: '/401',
      name: '401',
      component: TransportUnitDetail
    },
    {
      path: '/huashutuxiang',
      name: 'hua',
      component: huashutuxiang
    },
    {
      path: '/liuxin',
      name: 'liuxin',
      component: liuxin
    },
    {
      path: '/sin',
      name: 'sin',
      component: sin
    },
    {
      path: '/3D',
      name: 'D3',
      component: d3
    },
    {
      path: '/swiper',
      name: 'swiper',
      component: swiper
    },
    {
      path: '/rain',
      name: 'rain',
      component: rain
    },
    {
      path: '/websocket',
      name: 'websocket',
      component: websocket
    },
    {
      path: '/elementnotific',
      name: 'elementnotific',
      component: elementnotific
    },
    {
      path: '/shiping',
      name: 'shiping',
      component: shiping
    },
    {
      path: '/elment',
      name: 'elment',
      component: elment
    },
    {
      path: '/yanzhen',
      name: 'yanzhen',
      component: yanzhen
    },
    {
      path: '/daochuEXl',
      name: 'daochuEXl',
      component: daochuEXl
    },
    {
      path: '/love',
      name: 'love',
      component: love
    },
    {
      path: '/circleMenu',
      name: 'circleMenu',
      component: circleMenu
    },
    {
      path: '/yuanhuan',
      name: 'yuanhuan',
      component: yuanhuan
    },
    {
      path: '/table',
      name: 'table',
      component: table
    },
    {
      path: '/table2',
      name: 'table2',
      component: table2
    },
    {
      path: '/Menu',
      name: 'Menu',
      component: Menu
    },
    {
      path: '/Menuc',
      name: 'Menuc',
      component: Menuc
    },
    {
      path: '/lunxu',
      name: 'lunxu',
      component: lunxu
    },
    {
      path: '/qc',
      name: 'qc',
      component: qc
    },
    
  ]
})



